
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Card from 'vux/src/components/card/index.vue'

import { mapState, mapMutations } from 'vuex'
export default {
	components: {
		ViewBox,
		Tab,
		TabItem,
		Sticky,
		Flexbox,
		FlexboxItem,
		Group,
		Cell,
		Card
	},
	computed: {
		...mapState({
			tabName: state => state.discovery.topic.tabName,
			tabIndex: state => state.discovery.topic.activeIndex,
		})
	},
	data () {
		return{
			tabs: ['热门','最新','关注'],
			commends: [
			[
			{
				title: '求书',
				hasBorder: true
			},
			{
				title: '文学库',
				hasBorder: false
			}
			],
			[
			{
				title: '英语角',
				hasBorder: true
			},
			{
				title: '综合讨论',
				hasBorder: false
			}
			]
			]
		}
	},
	methods: {
		...mapMutations([
			'UPDATE_DISCOVERY_TOPIC_ACTIVEINDEX',
			'UPDATE_DISCOVERY_TOPIC_TABNAME'
		]),
		changeTabIndex (index, item) {
			this.UPDATE_DISCOVERY_TOPIC_ACTIVEINDEX(index);
			this.UPDATE_DISCOVERY_TOPIC_TABNAME(item);
		}
	}
}



