
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Rater from 'vux/src/components/rater/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { mapState, mapMutations } from 'vuex'
export default {
	components: {
		XHeader,
		ViewBox,
		Flexbox,
		FlexboxItem,
		Rater,
		LoadMore,
		Tabbar,
		TransferDom,
		Popup,
		XTextarea,
		Group
	},
	data () {
		return{
			showPopup: false,
			commentValue: ''
		}
	},
	props: {
		bookReviewDetails: {
			type: Object
		}
	},
	computed: {
		...mapState({
			u_id: state => state.user.u_id,
			u_name: state => state.user.u_name,
			u_avarta: state => state.user.u_avarta
		}),
	},
	methods: {
		...mapMutations([
			'UPDATE_DISCOVERY_ISSHOWBOOKREVIEWDETAILS'
			]),
		hideBookReviewDetails () {
			this.UPDATE_DISCOVERY_ISSHOWBOOKREVIEWDETAILS(false);
		},
		thumbUp () {
			if( this.bookReviewDetails.isLove ){
				this.bookReviewDetails.isLove = false;
				this.bookReviewDetails.lovenum--;
			}else{
				this.bookReviewDetails.isLove = true;
				this.bookReviewDetails.lovenum++;
			}
		},
		openPopup () {
			this.showPopup = true;
		},
		closePopup () {
			this.showPopup = false;
		},
		saveComment () {
			if( this.commentValue === '' ){
				console.log('输入为空');
				this.$vux.toast.text('请输入文字', 'middle')
				return;
			}
			var date = this.getNowFormatDate();
			var comment = {
				comment_avarta: this.u_avarta,
		    	comment_name: this.u_name,
		    	comment_content: this.commentValue,
		    	comment_date: date
			};
			this.bookReviewDetails.comments.push(comment);
			this.commentValue = '';
			this.showPopup = false;
			this.$vux.toast.text('回复成功', 'middle')
		},
		getNowFormatDate() {
		    let date = new Date();
		    let seperator1 = "-";
		    let seperator2 = ":";
		    let month = date.getMonth() + 1;
		    let strDate = date.getDate();
		    if (month >= 1 && month <= 9) {
		        month = "0" + month;
		    }
		    if (strDate >= 0 && strDate <= 9) {
		        strDate = "0" + strDate;
		    }
		    let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
		            + " " + date.getHours() + seperator2 + date.getMinutes();
		    return currentdate;
		}
	}	
}



