
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Rater from 'vux/src/components/rater/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'

import { mapState,mapMutations } from 'vuex'
import BookServer from '../../api/BookServer.js'
export default {
	components: {
		ViewBox,
		ButtonTab,
		ButtonTabItem,
		Flexbox,
		FlexboxItem,
		Swiper,
		SwiperItem,
		Tab,
		TabItem,
		Rater,
		XHeader,
		Popup,
		Group,
		Cell,
		XInput,
		XTextarea,
		Checker,
		CheckerItem,
		XButton,
		Search,
		Sticky
	},
	computed: {
		...mapState({
			bookStore: state => state.classify.bookStore
		}),
	},
	created () {
		//组件创建完成后获取数据
		this.fetchData();
	},
	watch: {
		//如果路由有变化，则执行该方法
		'$route': 'fetchData'
	},
	data () {
		return{
			search_value: '',
			results: [],
		}
	},
	methods: {
		...mapMutations([
			'UPDATE_BOOKINFO',
			'UPDATE_CLASSIFY_BOOKSTORE'
		]),
		fetchData () {
			//异步请求数据
			let self = this;
			BookServer.GET_BOOKSTORE_ALLBOOKS(this).then((res) => {
				self.UPDATE_CLASSIFY_BOOKSTORE(res.body)
			})
			setTimeout(() => {
			   	self.$vux.loading.hide()
			}, 300)
		},
		turnBookInfo (book_id) {
			//获取相应书籍Id的信息 
			//并通过mutations更新数据源 this.UPDATE_BOOKINFO()
			let self = this;
			BookServer.GET_BOOKINFO(this, book_id).then((res) => { 
			   	self.UPDATE_BOOKINFO(res.data);
			});
			setTimeout(() => {
			   	self.$vux.loading.hide()
				self.$router.push('/BookInfo');
			}, 300)
		},
		resultClick (item) {
			//获取相应书籍Id的信息 
			//并通过mutations更新数据源 this.UPDATE_BOOKINFO()
			let self = this;
			this.$vux.loading.show({
			 text: '加载中...'
			})
			BookServer.GET_BOOKINFO(this, item.book_id).then((res) => { 
			   	self.UPDATE_BOOKINFO(res.data);
			});
			setTimeout(() => {
			   	self.$vux.loading.hide()
				self.$router.push('/BookInfo');
			}, 300)
		},
		onFocus () {
			this.results = [];
		},
		onSubmit () {
			let self = this;
			BookServer.SEARCH_BOOKSTORE(this, this.search_value).then((res) => {
				for(let i = 0; i < res.data.length; i++){
					self.results.push( {
						title: res.data[i].book_name,
						other: i,
						book_id: res.data[i].book_id
					})
				}
				console.log(self.results)
			});
		},
		onCancel () {

		},
		getResult () {

		},
	}
}



