
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapState, mapMutations } from 'vuex'
export default{
	components: {
		ViewBox,
		Flexbox,
		FlexboxItem,
		Tab,
		TabItem,
		Popup,
		Checker,
		CheckerItem,
		XButton,
		Tabbar,
		Sticky,
		Swipeout,
		SwipeoutItem,
		SwipeoutButton 
	},
	computed: {
		...mapState({
			u_id: state => state.user.u_id,
			bookStore: state => state.bookBasket.bookStore,
			usedBook: state => state.bookBasket.usedBook
		}),
		bookStoreTotalPrice () { 
			if(this.bookStoreBookId === null){
				return 0;
			}else{
				//多选
				// for(let i = 0;i < this.bookStoreBookId.length;i++){

				// }
				return this.bookStoreBookId.book_borrowPric;
			}				
		},
		usedBookTotalPrice () { 
			if(this.usedBookId === null){
				return 0;
			}else{
				//多选
				// for(let i = 0;i < this.usedBookId.length;i++){
				// all += parseFloat()
				// }
				return this.usedBookId.book_sellPric;
			}
		}
	},
	methods: {
		...mapMutations([
			'DELETE_BOOKBASKET_BOOKSTORE',
			'DELETE_BOOKBASKET_USEDBOOK',
			'UPDATE_POSTORDERINFO'
			]),
		updateIndex (index) {
			this.index = index
		},
		deleteBookStoreBook (index) {
			const _this = this
			this.$vux.confirm.show({
				title: '移除确认',
				content: '确定把该图书从书篮中移除么',
				onCancel () {

				},
				onConfirm () {
					_this.DELETE_BOOKBASKET_BOOKSTORE(index);
					_this.$vux.toast.show({
						text: '移除成功',
						type: 'success',
						position: 'middle'
					})
				}
			})
		},
		deleteUsedBook (index) {
			const _this = this
			this.$vux.confirm.show({
				title: '移除确认',
				content: '确定把该图书从书篮中移除么',
				onCancel () {

				},
				onConfirm () {
					_this.DELETE_BOOKBASKET_USEDBOOK(index);
					_this.$vux.toast.show({
						text: '移除成功',
						type: 'success',
						position: 'middle'
					})
				}
			})
		},
		postOrder () {
			let date = {
				borrowDate: this.getNowFormatDate(),
				returnDate: this.getReturnFormatDate(this.getNowFormatDate().split(' ')[1])
			}
			console.log(this.tabName)
			if(this.tabName === '图书馆'){
				//图书馆中图书 进行结算订单
				let info = {
					from: '图书馆',
					date: date,
					index: this.index,
					bookStore: this.bookStoreBookId,
					usedBook: {
				    	book_id: null,
						book_name: null,
						book_banner: null,
						book_author: null,
						book_sellPric: null,
						book_depreciation: null,
						seller: {
							u_id: null,
							u_name: null,
							u_tell: null
						}
			    	}
				}
				let self = this;
				this.$vux.loading.show({
					text: '加载中...'
				});
				setTimeout(() => {
					self.UPDATE_POSTORDERINFO(info);
					self.$vux.loading.hide()
					self.$router.push('/User/postOrder')
				}, 300)
			}
			if(this.tabName === '二手书'){
				//二手书中图书 进行结算订单
				let info = {
					from: '二手书',
					date: date,
					index: this.index,
					bookStore: {
				    	book_id: null,
						book_name: null,
						book_banner: null,
						book_author: null,
						book_borrowPric: null
			    	},
					usedBook: this.usedBookId
				}
				let self = this;
				this.$vux.loading.show({
					text: '加载中...'
				});
				setTimeout(() => {
					self.UPDATE_POSTORDERINFO(info);
					self.$vux.loading.hide()
					self.$router.push('/User/postOrder')
				}, 300)	
			}
			return;
		},
		changeTabName (item) {
			this.tabName = item;
		},
		getNowFormatDate() {
			//获取当前时间
			let date = new Date();
			let seperator1 = "-";
			let seperator2 = ":";
			let month = date.getMonth() + 1;
			let strDate = date.getDate();
			if (month >= 1 && month <= 9) {
				month = "0" + month;
			}
			if (strDate >= 0 && strDate <= 9) {
				strDate = "0" + strDate;
			}
			let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
			+ " " + date.getHours() + seperator2 + date.getMinutes();
			return currentdate;
		},
		getReturnFormatDate(end) {
			//获取当前时间
			let date = new Date();
			let seperator1 = "-";
			let seperator2 = ":";
			let month = date.getMonth() + 1;
			let strDate = date.getDate()+7;
			if (month >= 1 && month <= 9) {
				month = "0" + month;
			}
			if (strDate >= 0 && strDate <= 9) {
				strDate = "0" + strDate;
			}
			let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
			+ " " + end;
			return currentdate;
		}

	},
	data () {
		return{
			max: 2,
			tabIndex: 0,
			tabName: '图书馆',
			tabs: ['图书馆','二手书'],
			bookStoreBookId: null,
			usedBookId: null,
			index: null
		}
	}
}



