
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Search from 'vux/src/components/search/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'

import { mapState,	mapMutations } from 'vuex'
import BookServer from '../../api/BookServer.js'
import DataBase from '../../api/DataBase.js'
export default {
	components: {
		ViewBox,
		Flexbox,
		FlexboxItem,
		Grid,
		GridItem,
		Search,
		Swiper,
		SwiperItem,
		Tab,
		TabItem,
		LoadMore,
		Sticky
	},
	created () {
		//组件创建完成后获取数据
		this.fetchData();
	},
	watch: {
		//如果路由有变化，则执行该方法
		'$route': 'fetchData'
	},
	data () {
		return{
			search_value: '',
			results: [],
			booktabIsLoading: false
		}
	},
	computed: {
		...mapState({
			tabTitle: state =>state.booktabsTitle,
			tabIndex: state => state.booktabsIndex,
			swipers: state => state.swipers,
			booktabs: state => state.booktabs
		})
	},
	methods: {
		...mapMutations([
			'UPDATE_BOOKINFO',
			'UPDATE_INDEX_TAB',
			'UPDATE_INDEX_BOOKTABSINDEX',
			'UPDATE_INDEX_BOOKTABSTITLE'
			]),
		fetchData () {
			//异步请求数据
			this.get_book_data('文学', 0);
			console.log('执行获取数据函数')
		},
		resultClick () {

		},
		onFocus () {

		},
		onSubmit () {

		},
		onCancel () {

		},
		getResult () {

		},
		get_book_data (booktab, index) {
			this.UPDATE_INDEX_BOOKTABSINDEX(index);
			this.UPDATE_INDEX_BOOKTABSTITLE(booktab);
			this.booktabIsLoading = true;
			let self = this;
			setTimeout(() => {
				//发起异步请求 进行获取相应数据
				DataBase.GET_INDEX_BASEDATA(this, index+1).then((res) => {
					self.UPDATE_INDEX_TAB({index: index, books: res.body});
				});
				self.booktabIsLoading = false;
			},1000)
			
		},
		turnBookInfo (book_id) {
			let self = this;
			BookServer.GET_BOOKINFO(this, book_id).then((res) => { 
				self.UPDATE_BOOKINFO(res.data);
			});
			setTimeout(() => {
				self.$vux.loading.hide()
				self.$router.push('/BookInfo');
			}, 300)
		}
	}
}



