
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Card from 'vux/src/components/card/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState, mapMutations } from 'vuex'
export default{
	directives: {
		TransferDom
	},
	components: {
		ViewBox,
		Flexbox,
		FlexboxItem,
		Swiper,
		SwiperItem,
		Tab, 
		TabItem,
		Group,
		Cell,
		LoadMore,
		Card,
		XDialog,
		Qrcode,
		Divider
	},
	computed: {
		...mapState({
			user: state => state.user,
			order_all: state => state.order.order_all,
			order_waitPay: state => state.order.order_waitPay,
			order_waitRefresh: state => state.order.order_waitRefresh,
			order_waitComment: state => state.order.order_waitComment,
			order_refund: state => state.order.order_refund
		}),
		tabIndex () {
			let index = 0;
			let tabIndex = this.$route.params.tabIndex;
			let self = this;
			switch( tabIndex ){
				case 'allOrder':
				index = 0;
				self.tabName = '全部';
				break;
				case 'waitPay':
				index = 1;
				self.tabName = '待付款';
				break;
				case 'waitRefresh':
				index = 2;
				self.tabName = '待归还';
				break;
				case 'waitComment':
				index = 3;
				self.tabName = '待评价';
				break;
				case 'refund':
				index = 4;
				self.tabName = '退款单';
				break;			
			}
			return index;
		}
	},
	methods: {
		...mapMutations([

			]),
		changeContent (index) {
			switch( index ){
				case 0:
				this.$router.push('/User/userOrder/allOrder');
				break;
				case 1:
				this.$router.push('/User/userOrder/waitPay');
				break;
				case 2:
				this.$router.push('/User/userOrder/waitRefresh');
				break;
				case 3:
				this.$router.push('/User/userOrder/waitComment');
				break;
				case 4:
				this.$router.push('/User/userOrder/refund');
				break;
			}
		},
		showOrderDialog (item) {
			
			this.orderInfo = item;
			console.log(this.orderInfo)
			this.showOrderInfo = true;
		}
	},
	data () {
		return{
			showOrderInfo: false,
			tabName: '全部',
			tabs: ['全部','待付款','待归还','待评价','退款单'],
			orderInfo: {
				from: null,
				date: {
					borrowDate: null,
					returnDate: null,
				},
				bookInfo: {
					book_id: null,
					book_name: null,
					book_banner: null,
					book_author: null,
					book_sellPric: null,
					book_depreciation: null,
					seller: {
						u_id: null,
						u_name: null,
						u_tell: null
					}
				}
			}
		}
	}
}



