
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import { mapState, mapMutations } from 'vuex'
import BookServer from '../../api/BookServer.js'
export default{
	components: {
		ViewBox,
		XHeader,
		Flexbox,
		FlexboxItem
	},
	computed: {
		...mapState({
			direction: state => state.direction,
			hot: state => state.indexGrid.hot
		}),
		headerTransition () {
			return this.direction === 'forward' ? 'vux-header-fade-in-right' : 'vux-header-fade-in-left'
		}
	},
	methods: {
		...mapMutations([
			'UPDATE_BOOKINFO'
			]),
		turnBookInfo (book_id) {
			let self = this;
			this.$vux.loading.show({
				text: '加载中...'
			});
			BookServer.GET_BOOKINFO(this, book_id).then((res) => { 
				self.UPDATE_BOOKINFO(res.data);
			});
			setTimeout(() => {
				self.$vux.loading.hide()
				self.$router.push('/BookInfo');
			}, 300)
		}
	}
}



