
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState, mapMutations } from 'vuex'
import Comment from '@/components/Comment/Comment'
export default{
	directives: {
		TransferDom
	},
	components: {
		XHeader,
		ViewBox,
		Flexbox,
		FlexboxItem,
		Swiper,
		SwiperItem,
		Tab,
		TabItem,
		Actionsheet,
		Alert,
		Previewer,
		'comment-item' : Comment
	},
	data () {
		return{
			actionsmenusSwich: false,
			tabIndex: 0,
			tabName: '简介',
			tabs: ['简介','目录'],
			actionsmenus: {
				borrwow: '立即借阅',
				addBookCar: '加入书篮'
			}
		}
	},
	computed: {
		...mapState({
			uid: state => state.u_id,
			bookInfo: state => state.bookInfo
		}),
		bookLists () {
			if(this.bookInfo.book_catalog === ''){	
				return "暂无数据"
			}else{
				return this.bookInfo.book_catalog.split('/')
			}
		}
	},
	methods: {
		...mapMutations([
			'ADD_BOOKBASKET_BOOKSTORE'
		]),
		doBorrwow (book_id, book_inventory) {
			//判断库存量是否为0
			if(book_inventory === 0){
				this.$vux.alert.show({
				  title: '失败',
				  content: '该书库存不足，看看其他的吧',
				  onShow () {
				    
				  },
				  onHide () {
				    
				  }
				})
				return;
			}
			//进行立即借阅的动作
			
		},
		doAddBookCar () {
			let book = {
				book_id: this.bookInfo.book_id,
				book_name: this.bookInfo.book_name,
				book_banner: this.bookInfo.book_banner,
				book_author: this.bookInfo.book_author,
				book_borrowPric: this.bookInfo.book_borrowPric
			};
			this.ADD_BOOKBASKET_BOOKSTORE(book);
			this.$vux.toast.show({
				text: '添加成功',
				type: 'success',
				position: 'middle'
			});
		},
		openActionsmenus () {
			this.actionsmenusSwich = true;
		}
	}
}



