
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Rater from 'vux/src/components/rater/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'

import { mapState, mapMutations } from 'vuex'
import BookServer from '../../api/BookServer.js'
import BookReviewDetails from './BookReviewDetails'
export default {
	components: {
		ViewBox,
		ButtonTab,
		ButtonTabItem,
		Flexbox,
		FlexboxItem,
		Swiper,
		SwiperItem,
		Tab,
		TabItem,
		Rater,
		LoadMore,
		BookReviewDetails,
		Sticky
	},
	computed: {
		...mapState({
			direction: state => state.direction,
			isShowBookReviewDetails: state => state.discovery.book_review.isShowBookReviewDetails,
			tabName: state => state.discovery.book_review.tabName,
			tabIndex: state => state.discovery.book_review.activeIndex,
			hots: state => state.discovery.book_review.hots,
			newItems: state => state.discovery.book_review.news
		})
	},
	created () {
		//组件创建完成后获取数据
		this.fetchData();
	},
	watch: {
		//如果路由有变化，则执行该方法
		'$route': 'fetchData'
	},
	data () {
		return{
			showtabs: true,
			headIndex: 0,
			tabs: ['热门','最新','关注'],
			bookReviewDetails: {}
		}
	},
	methods: {
		...mapMutations([
			'UPDATE_BOOKINFO',
			'UPDATE_DISCOVERY_ACTIVEINDEX',
			'UPDATE_DISCOVERY_TABNAME',
			'UPDATE_DISCOVERY_ISSHOWBOOKREVIEWDETAILS'
			]),
		fetchData () {
			//异步请求数据
			console.log('执行获取数据函数')
		},
		turnBookInfo (book_id) {
			//获取相应书籍Id的信息 
			//并通过mutations更新数据源 this.UPDATE_BOOKINFO()
			let self = this;
			BookServer.GET_BOOKINFO(this, book_id).then((res) => { 
			   	self.UPDATE_BOOKINFO(res.data);
			});
			setTimeout(() => {
			   	self.$vux.loading.hide()
				self.$router.push('/BookInfo');
			}, 300)
		},
		changeTabIndex (index,tab) {
			this.UPDATE_DISCOVERY_TABNAME(tab);
			this.UPDATE_DISCOVERY_ACTIVEINDEX(index);
		},
		showBookReviewDetails (data) {
			let self = this;
			this.bookReviewDetails = data;
			this.$vux.loading.show({
			 text: '加载中...'
			});
			setTimeout(() => {
				self.$vux.loading.hide()
				self.UPDATE_DISCOVERY_ISSHOWBOOKREVIEWDETAILS(true);
			}, 300);
		}
	}
}



