
	import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Rater from 'vux/src/components/rater/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

	import { mapState,mapMutations } from 'vuex'
	import userServer from '../../api/userServer.js'
	export default {
		components: {
			ViewBox,
			ButtonTab,
			ButtonTabItem,
			Flexbox,
			FlexboxItem,
			Swiper,
			SwiperItem,
			Tab,
			TabItem,
			Rater,
			XHeader,
			Popup,
			Group,
			Cell,
			XInput,
			XTextarea,
			Checker,
			CheckerItem,
			XButton
		},
		computed: {
			...mapState({
				user: state => state.user,
				direction: state => state.direction,
				hots: state => state.discovery.book_review.hots,
				bookItems: state => state.user.u_books
			}),
			headIndex () {
				if( this.$route.path === '/Discovery/' ){
					return 0;
				}
				if( this.$route.path === '/Discovery/topic'){
					return 1;
				}
			}
		},
		data () {
			return{
				showBookReviewPopup: false,
				showTopicPopup: false,
				chooseBookPopup: false,
				bookName: '',
				book: {},
				BookReviewFromData: {
					title: '',
					score: 0,
					comtent: ''
				},
				TopicFromData: {
					title: '',
					comtent: ''
				}
			}
		},
		methods: {
			...mapMutations([
					'UPDATE_DISCOVERY_NEWS',
					'UPDATE_USER_U_BOOKS'
				]),
			getUserBooks () {
				let self = this;
				// userServer.GET_USER_USEDBOOK(this, this.u_id).then((res) => {
				// 	setTimeout(() => {
				// 		self.$vux.loading.hide();
				// 		console.log(res.body)
				// 		// self.UPDATE_USER_U_BOOKS(res.body);
				// 	}, 500)
						
				// });
				this.chooseBookPopup = true;
			},
			trunTopic () {
				this.$router.push('/Discovery/topic');
			},
			trunFind () {
				this.$router.push('/Discovery/');
			},
			doActions () {
				if( this.$route.path === '/Discovery/' ){
					this.showBookReviewPopup = true;
					return;
				}
				if( this.$route.path === '/Discovery/topic'){
					this.showTopicPopup = true;
					return;
				}
				
			},
			closePopup () {
				if( this.$route.path === '/Discovery/' ){
					this.showBookReviewPopup = false;
					this.chooseBookPopup = false;
					return;
				}
				if( this.$route.path === '/Discovery/topic'){
					this.showTopicPopup = false;
					this.chooseBookPopup = false;
					return;
				}
			},
			onItemClick (bookItem) {
				this.book = bookItem;
				this.chooseBookPopup = false;
			},
			postBookReview () {
				let bookReview = {
					u_id: this.user.u_id,	
    				u_avarta: this.user.u_avarta,
    				u_name: this.user.u_name,
    				score: this.BookReviewFromData.score,
    				comment_content: this.BookReviewFromData.content,
    				comment_book: this.book,
    				date: this.getNowFormatDate(),
    				comments: [
    					
    				],
    				isLove: false,
    				lovenum: 0
				}
				//开启异步操作 像后端发送数据
				//未写 与后端交互代码
				this.UPDATE_DISCOVERY_NEWS(bookReview);

				//重置数据
				this.bookName = '';
				this.book = {};
				this.BookReviewFromData = {
					title: '',
					score: 0,
					comtent: ''
				};

				//关闭发书评的Popup
				this.showBookReviewPopup = false;

				//给用户展示成功提示
				this.$vux.toast.show({
				 	text: '发表成功',
				 	type: 'success',
				 	position: 'middle'
				})
			},
			getNowFormatDate() {
				//获取当前时间
			    let date = new Date();
			    let seperator1 = "-";
			    let seperator2 = ":";
			    let month = date.getMonth() + 1;
			    let strDate = date.getDate();
			    if (month >= 1 && month <= 9) {
			        month = "0" + month;
			    }
			    if (strDate >= 0 && strDate <= 9) {
			        strDate = "0" + strDate;
			    }
			    let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
			            + " " + date.getHours() + seperator2 + date.getMinutes();
			    return currentdate;
			}
		}
	}



