
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import { mapState } from 'vuex'
export default {
	components: {
		Flexbox,
		FlexboxItem,
		Group,
		Cell,
		Grid,
		GridItem
	},
	data () {
		return{
			turnIndex: ['全部','待付款','待归还','待评价','退款单']
		}
	},
	created () {
		//组件创建完成后获取数据
		this.fetchData();
	},
	watch: {
		//如果路由有变化，则执行该方法
		'$route': 'fetchData'
	},
	computed: {
		...mapState({
			u_id: state => state.user.u_id,
	    	u_avarta: state => state.user.u_avarta,
	    	u_name: state => state.user.u_name,
	    	u_wallet: state => state.user.u_wallet,
	    	u_integration: state => state.user.u_integration,
	    	u_coupon: state => state.user.u_coupon
		})
	},
	methods: {
		fetchData () {
			//异步请求数据
			console.log('执行获取数据函数')
		}
	}
}



