
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapState, mapMutations } from 'vuex'
import userServer from '../../api/userServer.js'
export default{
	components: {
		Flexbox,
		FlexboxItem,
		Swipeout,
		SwipeoutItem,
		SwipeoutButton,
		XButton 
	},
	created () {
		this.fetchData();
	},
	computed: {
		...mapState({
			u_id: state => state.user.u_id,
			u_books: state => state.user.u_books
		})
	},
	methods: {
		...mapMutations([
				'DELETE_USER_MYBOOK',
				'UPDATE_USER_U_BOOKS'
			]),
		fetchData () {
			let self = this;
			userServer.GET_USER_USEDBOOK(this, this.u_id).then((res) => {
				setTimeout(() => {
					self.$vux.loading.hide();
					self.UPDATE_USER_U_BOOKS(res.body);
				}, 500)
					
			});
			
			
		},
		deleteMyBook (index) {
			const _this = this
			this.$vux.confirm.show({
				title: '移除确认',
				content: '确定把该图书移除么',
				onCancel () {

				},
				onConfirm () {
					_this.DELETE_USER_MYBOOK(index);
					_this.$vux.toast.show({
						text: '移除成功',
						type: 'success',
						position: 'middle'
					})
				}
			})
		}
	}
}



